video {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
}
img {
    max-width: 100%;
    height: 250px;
    border: 10px ;
    padding: 15px;
}

.hero-container {
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: insert 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
}

.content {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
}
.content >h1 {
    text-shadow: 4px 4px 4px red;
    letter-spacing: 6px;
    height: 80px;
    width: 100%;
    text-align: center;
    font-weight: 900;
    font-size: 40px;

}
.content >h2 {
    text-shadow: 4px 4px 4px black;
}
.content >p {
    text-indent: 50px;
  text-align: justify;
  letter-spacing: 3px;
  margin-left: 50px;
  margin-right: 50px;
}

